import { Button } from "@webs/component-library/button";

import { addCodeExample } from "./helpers/code-example";

export default {
  title: "Example/Button",
  argTypes: {
    backgroundColor: {
      control: { type: "select", options: ["violet", "pink"] },
    },
    label: {
      control: {
        type: "select",
        options: ["Short", "Medium Length", "Rather Long Button Text"],
      },
    },
    onclick: "",
  },
};

const Template = (args) => {
  return `
    <webs-button
      text=${args.label}
      extraClasses="${args.backgroundColor}"
      href=""
      openInNewTab="true"
    ></webs-button>
  `;
};

export const Primary = Template.bind({
  backgroundColor: `"violet"`,
  label: "Short",
});

